.app{
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  padding: 20px;
}

.app-container{
  background-color: #1A1A40;
  padding: 30px 20px 0 20px;
  height: fit-content;
  margin-top: 50px;
  border-radius: 10px;
  max-width: 100%;
  width: 500px;
}

.app-container h1{
  color: white;
  text-align: center;
  padding: 25px 0;
}

.to-do {
  width: 100%;
  }

  .to-do  form{
    display: flex;
    width: 100%;
  }

  .to-do form input {
    width: 75%;
    box-sizing: border-box;
    padding: 15px 10px 15px 15px;
    border: none;
    border-radius: 20px 0 0 20px;
  }
  
  .to-do form button {
    width: 25%;
    background-color: #8758ff;
    color: white;
    border: none;
    cursor: pointer;
    border-radius: 0 20px 20px 0;
    font-weight: 600;
    
  }

  .to-do .search{
    width: 100%;
    border-radius: 20px;
    padding: 15px 10px 15px 15px;
    margin: 10px 0 0 0;
    border: none;
  }

  .to-do input:focus{
    outline: none;
  }

  .to-do-list{
    padding: 30px 0 0 0;
    display: flex;
    justify-content: center;
    color: white;
  }

  .to-do-list h5{
    padding-bottom: 20px;
    text-align: center;
  }

  .css-1m9pwf3{
    width: 300px !important;
  }

  .to-do-list ul{
    list-style: none;
    width: 100%;
  }
  
  .to-do-list ul li{
    background-color: #8758ff;
    border-radius: 8px;
    margin-bottom: 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
  }

  .item-text{
    display: flex;
    align-items: center;
  }

  .icons{
    padding-right: 20px;
  }

  .edit-icon, .delete-icon{
    margin-left: 15px;
    cursor: pointer;
  }
  
  .checked{
    text-decoration: line-through;
  } 


  @media only screen and (max-width: 600px){
    .css-1m9pwf3{
      width: 200px !important;
    }

    .to-do form button {
      padding: 0 10px;
    }

  }